export default {
	data () {
		return {
			collected: false
		}
	},
	methods: {
		tel(tel) {
			uni.makePhoneCall({
				phoneNumber: tel //仅为示例
			});
		},
		preview(urls) {
			uni.previewImage({
				urls: urls,
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(res) {
						console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					},
					fail: function(res) {
						console.log(res.errMsg);
					}
				}
			});
		},
		clearCol () {
			this.collected = false
		},
		addCol () {
			this.collected = true
		}
	}
}
